import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import prize_list from '@/components/prize_list'
import prize_edit from '@/components/prize_edit'
import prize_add from '@/components/prize_add'
import member from '@/components/member'
import draw_view_employee from '@/components/draw_view_employee'
import annua_member from '@/components/annual_member'
import draw_view_family from '@/components/draw_view_family'
import draw_list_edit from '@/components/draw_list_edit'
import draw_list_f_edit from '@/components/draw_list_f_edit'
Vue.use(Router)


export default new Router({
    routes : [
        {path:'/',component:Home},
        {path:'/prize_list',component:prize_list},
        {path:'/prize_edit',component:prize_edit},
        {path:'/prize_add',component:prize_add},
        {path:'/member_list',component:member},
        {path:'/draw_list/employee',component:draw_view_employee},
        {path:'/draw_list/edit_e',component:draw_list_edit},
        {path:'/draw_list/edit_f',component:draw_list_f_edit},
        {path:'/draw_list/family',component:draw_view_family},
        {path:'/annual_member_list',name:'annual_member',component:annua_member}
    ]
})


